.card {
    width: 150px;
    height: 200px;
    /* background-color: #fff; */
    color: black;
    position: relative;
}

.front__side,
.back__side {
    width: 100%;
    height: 100%;
    overflow: hidden;
    position: absolute;
    backface-visibility: hidden;
    -webkit-backface-visibility: hidden;
    transition: transform 0.5s ease;
    border-radius: 10px;
}

.back__side {
    background-color: #fff;
    display: flex;
    justify-content: center;
    align-items: center;
    transform: perspective(600px) rotateY(180deg);
}

.active__card .redSide {
    color: white;
    background-color: red;
}

.active__card .blackSide {
    color: white;
    background-color: black;
}

.front__side {
    transform: perspective(600px) rotateY(0);
}

.flipped__card .front__side {
    transform: perspective(600px) rotateY(-180deg);
}

.flipped__card .back__side {
    transform: perspective(600px) rotateY(0);
}

.front__side img {
    width: 100%;
    height: 100%;
}

.guessed__item {
    pointer-events: none;
    opacity: 0;
    visibility: hidden;
}
