.container {
    margin: 30px 0 0 30px;
    display: flex;
    flex-wrap: wrap;
    column-gap: 20px;
    row-gap: 20px;
    transition: 1s;
    margin-bottom: 20px;
    position: relative;
}
.container h1,
.container h2 {
    color: white;
}
.container h2 {
    margin-bottom: 45px;
}

.container button {
    height: 50px;
    width: 200px;
    color: white;
    background: transparent;
    border: 1px solid #61dbfb;
    font-weight: 600;
    font-size: 22px;
    margin-bottom: 20px;
}

.win {
    position: absolute;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 100%;
    background: rgba(0, 0, 0, 0.5);
}

.users__info {
    padding: 0 50px;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.user {
    display: flex;
    align-items: center;
}

.user:last-child {
    flex-direction: row-reverse;
}
.score {
    color: white;
    font-size: 22px;
    font-family: sans-serif;
    margin-left: 30px;
}
.user:last-child .score {
    margin-right: 30px;
}

.userPhoto {
    width: 150px;
    height: 150px;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 50%;
    overflow: hidden;
}

.userPhoto img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}
